/**
iconsfont Webfont
*/
@font-face {
	font-family: 'iconsfont';
	src: url('../assets/fonts/icons/iconsfont.eot?1677803655897');
	src: url('../assets/fonts/icons/iconsfont.eot?#iefix-1677803655897') format('embedded-opentype'),
		url('../assets/fonts/icons/iconsfont.woff2?1677803655897') format('woff2'),
		url('../assets/fonts/icons/iconsfont.woff?1677803655897') format('woff'),
		url('../assets/fonts/icons/iconsfont.ttf?1677803655897') format('truetype');
	font-weight: normal;
	font-style: normal;
}

[class^='icon-']::before,
[class*=' icon-']::before {
	font-family: 'iconsfont';
	display: inline-block;
	vertical-align: middle;
	line-height: 1;
	font-weight: normal;
	font-style: normal;
	speak: none;
	text-decoration: inherit;
	font-size: inherit;
	text-transform: none;
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

