}

.rte h1,
.h1 {
  font-size: 48px;
}

.rte h2,
.h2 {
  font-size: 36px;
}

.rte h3,
.h3 {
  font-size: 30px;
}

.rte h4,
.h4 {
  font-size: 24px;
}

.rte h5,
.h5 {
  font-size: 20px;
}

.rte h6,
