}

img {
  max-width: 100%;
}

button,
input[type=button],
input[type=submit] {
  background: none;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
}

a,
button {
  appearance: none;
  outline: none;
}

.btn {
  background-color: #000;
  color: #fff;
  display: inline-block;
  font-size: 0;
  font-weight: bold;
  height: 40px;
  line-height: 40px;
  padding: 0 20px;
  transition: 0.4s ease-out;
}
