input,
textarea {
  background: #fff;
  border-radius: 0;
  box-shadow: none;
  color: #000;
  height: 25px;
  padding: 0 10px;
}
input::placeholder,
textarea::placeholder {
  color: #000;
}

@supports (-webkit-touch-callout: none) {
  select,
  textarea,
  input {
    font-size: 16px !important;
  }
}
