.header {
  max-width: 1278px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 500;
}

.header__container {
  background: linear-gradient(90deg, #E8EAF1 2.4%, rgba(235, 236, 240, 0) 98.82%);
  border-radius: 50px;
  border: 1px solid #fff;
  padding: 10px 30px;
  box-shadow: -6px -7px 11px 0px rgba(255, 255, 255, 0.84);
  box-shadow: 8px 7px 21px 0px rgba(0, 0, 0, 0.15);
  display: flex;
  justify-content: center;
  align-items: center;
}
@media only screen and (max-width: 1199.98px) {
  .header__container {
    top: 20px;
  }
}

.header__logo {
  margin-right: auto;
  position: relative;
}

.header__logo-img {
  border-radius: 100%;
  width: 63px;
  box-shadow: 5px 5px 10px 0px rgba(166, 180, 200, 0.7);
}
@media only screen and (max-width: 767.98px) {
  .header__logo-img {
    width: 45px;
  }
}

.header__hamburger-open {
  font-size: 21px;
  line-height: 21px;
  padding: 20px;
  color: #000;
}
@media only screen and (min-width: 1200px) {
  .header__hamburger-open {
    display: none;
  }
}
@media only screen and (max-width: 767.98px) {
  .header__hamburger-open {
    padding: 10px;
  }
}

.header__logo-link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: inline-block;
}

@media only screen and (max-width: 1199.98px) {
  .nav {
    position: fixed;
    top: -30px;
    right: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease-in-out;
  }
  .nav.nav--open {
    opacity: 1;
    pointer-events: initial;
  }
  .nav.nav--open .nav__items {
    transform: translateX(0);
  }
}

.nav__items {
  display: flex;
  align-items: center;
}
@media only screen and (max-width: 1199.98px) {
  .nav__items {
    display: flex;
    flex-direction: column;
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    height: 100%;
    background-color: #EAECF2;
    justify-content: center;
    align-items: center;
    transform: translateX(100%);
    transition: transform 0.4s ease-in-out;
  }
}
@media only screen and (max-width: 767.98px) {
  .nav__items {
    width: 100%;
  }
}

.nav__item .nav__item-link {
  font-size: 16px;
  font-weight: 300;
  line-height: 20px;
  color: #000;
  padding: 0 20px;
  position: relative;
}
.nav__item .nav__item-link::after {
  content: "";
  width: 0;
  height: 1px;
  background-color: #000;
  position: absolute;
  bottom: 0;
  left: 20px;
  transition: width 0.3s ease;
}
@media only screen and (min-width: 1025px) and (hover: hover) {
  .nav__item .nav__item-link:hover::after {
    width: calc(100% - 40px);
  }
}
@media only screen and (max-width: 1199.98px) {
  .nav__item {
    margin-bottom: 25px;
  }
  .nav__item .nav__item-link {
    font-size: 20px;
  }
}

.nav__item--close {
  position: absolute;
  top: 15px;
  right: 15px;
  font-size: 21px;
  line-height: 21px;
  padding: 10px;
}
.nav__item--close .header__hamburger-close {
  color: #000;
}
@media only screen and (min-width: 1200px) {
  .nav__item--close {
    display: none;
  }
}

.nav__item--btn {
  margin-left: 150px;
  margin-right: 29px;
}
.nav__item--btn .nav__item-link {
  background: transparent;
  border-radius: 31px;
  display: inline-block;
  width: 235px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  text-align: center;
  font-weight: 500;
  color: #fff;
  box-shadow: 6px 5px 10px 3px rgba(54, 54, 54, 0.24);
  position: relative;
  overflow: hidden;
}
.nav__item--btn .nav__item-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90.16deg, #676767 2.17%, #222221 50.45%, #000000 98.39%);
  z-index: -1;
}
.nav__item--btn .nav__item-link::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 150%;
  height: 100%;
  background: linear-gradient(-50.16deg, #676767 2.17%, #222221 50.45%, #000000 70.39%);
  z-index: -1;
  opacity: 0;
  transition: opacity 0.4s ease-in-out;
}
@media only screen and (min-width: 1025px) and (hover: hover) {
  .nav__item--btn .nav__item-link:hover::after {
    width: 100%;
    opacity: 1;
  }
}
@media only screen and (max-width: 1199.98px) {
  .nav__item--btn {
    margin-right: 0;
    margin-left: 0;
  }
}

